import { useEffect, useState } from "react";

// react-router components
import { Routes, Route, Navigate, useLocation } from "react-router-dom";

// @mui material components
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";

// Material Kit 2 React themes
import theme from "assets/theme";
import Presentation from "layouts/pages/presentation";

// Material Kit 2 React routes
import routes from "routes";

import LanguageProvider from "./Helpers/MultiSprk/LanguageContext";
import Register from "./pages/LandingPages/Register/index";

//import Strings from "Helpers/Strings/Strings";

export default function App() {
  const { pathname } = useLocation();
  // eslint-disable-next-line no-unused-vars
  const [language, setLanguage] = useState("no");

  // Setting page scroll to 0 when changing the route
  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
  }, [pathname]);

  const getRoutes = (allRoutes) =>
    allRoutes.map((route) => {
      if (route.collapse) {
        return getRoutes(route.collapse);
      }

      if (route.route) {
        return <Route exact path={route.route} element={route.component} key={route.key} />;
      }

      return null;
    });

  return (
    <LanguageProvider language={language} setLanguage={setLanguage}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Routes>
          {getRoutes(routes)}
          <Route path="/presentation" element={<Presentation />} />
          <Route path="/authentication/sign-up" element={<Register />} />
          {/** M fix, kan ikke vre rett i filen, direkte ustrukturert. Usystematisk. */}
          <Route path="*" element={<Navigate to="/presentation" />} />
        </Routes>
      </ThemeProvider>
    </LanguageProvider>
  );
}
